<template>
    <div class="my-container">
        <div>
            <div class="flex-row align-items">
                <span class="text-nowrap mr10 font20"> :</span>
                <span class="font24">{{title}}</span>
            </div>
        </div>
        <!--  -->
        <Single 
        v-if="singleQuestion.length"
        :isAnswer='isAnswer'
        :disabled='disabled'
        :singleQuestion='singleQuestion'></Single>
        <!--  -->
        <Multiple 
        v-if="multipleQuestion.length"
        :isAnswer='isAnswer'
        :disabled='disabled'
        :multipleQuestion='multipleQuestion'></Multiple>
        <!--  -->
        <Judgement 
        v-if="judgementQuestion.length"
        :isAnswer='isAnswer'
        :disabled='disabled'
        :judgementQuestion='judgementQuestion'></Judgement>
        <!--  -->
        <Answer 
        v-if="answerQuestion.length"
        :isAnswer='isAnswer'
        :disabled='disabled'
        :answerQuestion='answerQuestion'></Answer>
        <div class="mt20">
            <el-button type="success" @click="goBack"></el-button>
        </div>
    </div>
</template>

<script>
import { mapGetters } from "vuex";
import { post, get } from "../util/http.js";
export default {
  data() {
    return {
      //
      title: "",
      // 
      singleQuestion: [],
      // 
      multipleQuestion: [],
      // 
      judgementQuestion: [],
      // 
      answerQuestion: [],
      isAnswer: true,
      disabled:true
    };
  },
  methods: {
    // 
    goBack() {
      this.$router.back();
    }
  },
  async created() {
    try {
      let { id } = this.$route.params;
      const result = await get("/api/questions/getAnswerQuestionById/" + id);
      if (result.statusCode == 200) {
        this.title = result.data.answerDetail.title;
        this.singleQuestion = result.data.answerDetail.single.question;
        this.multipleQuestion =
          result.data.answerDetail.multiple.question;
        this.judgementQuestion = result.data.answerDetail.judgement.question;
        this.answerQuestion = result.data.answerDetail.answer.question;
      } else {
        this.$message({
          showClose: true,
          message: result.message,
          type: "warning"
        });
      }
    } catch (error) {
      this.$message({
        showClose: true,
        message: error.toString(),
        type: "error"
      });
    }
  },
  computed: {
    ...mapGetters(["userInfo"])
  }
};
</script>

<style lang="less" scoped>
.my-container {
  padding: 20px;
}
.min-width {
  min-width: 300px;
}
</style>


