<template>
  <div id="fileContent" class="my-container">
    <p class="mb10" style="color:red;"></p>
    <el-button type="primary" @click="openFile"></el-button>
    <el-button type="success" @click="downLoadTemplate"></el-button>
    <input
      ref="pathClear"
      type="file"
      style="display:none"
      id="file"
      @change="upload"
      accept="application/vnd.ms-excel, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet"
    >
    <div class="mt20 flex-row align-items">
      <h1 style="font-size:20px;"></h1>
      <el-select v-model="questionType" placeholder="">
        <el-option label="" value=""></el-option>
        <el-option label="" value=""></el-option>
        <el-option label="" value=""></el-option>
        <el-option label="" value=""></el-option>
        <el-option label="" value=""></el-option>
        <el-option label="" value=""></el-option>
        <el-option label="" value=""></el-option>
      </el-select>
    </div>
    <div class="mt20">
      <div class="flex-row flex-center">
        <span class="text-nowrap mr10 font20"> :</span>
        <el-input v-model.trim="questionTitle.name" clearable placeholder=""></el-input>
      </div>
      <div style="margin:10px 0 0 100px;">
        <span style="color:red;">{{questionTitle.message}}</span>
      </div>
    </div>
    <!--  -->
    <Single :singleQuestion="singleQuestion" @addSingle="addSingle" @deleteItem="deleteItem"></Single>
    <!--  -->
    <Multiple
      :multipleQuestion="multipleQuestion"
      @addMultiple="addMultiple"
      @deleteItem="deleteItem"
    ></Multiple>
    <!--  -->
    <Judgement
      :judgementQuestion="judgementQuestion"
      @addJudgement="addJudgement"
      @deleteItem="deleteItem"
    ></Judgement>
    <!--  -->
    <Answer :answerQuestion="answerQuestion" @addAnswer="addAnswer" @deleteItem="deleteItem"></Answer>
    <div class="mt20">
      <h1 style="font-size:20px;margin-bottom:20px;"></h1>
      <el-checkbox-group v-model="checkList">
        <el-checkbox label=""></el-checkbox>
        <el-checkbox label=""></el-checkbox>
        <el-checkbox label=""></el-checkbox>
        <el-checkbox label=""></el-checkbox>
        <el-checkbox label=""></el-checkbox>
        <el-checkbox label=""></el-checkbox>
        <el-checkbox label=""></el-checkbox>
      </el-checkbox-group>
    </div>
    <div class="mt20">
      <el-button type="primary" @click="create"></el-button>
      <el-button type="warning" @click="resetData"></el-button>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import { post, get } from "../util/http.js";
import { getRandomStr } from "../util/index.js";
import storage from "good-storage";
import img from '../assets/default.png'
export default {
  data() {
    return {
      // 
      questionTitle: {
        name: "",
        message: ""
      },
      // 
      singleQuestion: [],
      // 
      multipleQuestion: [],
      // 
      judgementQuestion: [],
      // 
      answerQuestion: [],
      checkList: [
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        ""
      ],
      questionType: "",
      isSave: 0
    };
  },
  created() {
    let params = this.$route.params;
    if (params.flag) {
      this.singleQuestion = params.single;
      this.multipleQuestion = params.multiple;
      this.judgementQuestion = params.judgement;
      this.answerQuestion = params.answer;
    } else {
      let obj = storage.get("AddQuestion");
      if (obj) {
        obj.singleQuestion && (this.singleQuestion = obj.singleQuestion);
        obj.multipleQuestion && (this.multipleQuestion = obj.multipleQuestion);
        obj.judgementQuestion &&
          (this.judgementQuestion = obj.judgementQuestion);
        obj.answerQuestion && (this.answerQuestion = obj.answerQuestion);
        obj.questionTitle && (this.questionTitle = obj.questionTitle);
        obj.questionType && (this.questionType = obj.questionType);
        obj.checkList && (this.checkList = obj.checkList);

        //
        var n = new Notification('', {
          body: '',
          tag: 'avenstar', //tag
          icon: img,
          requireInteraction: false //false
        })
      }
    }
  },
  mounted() {
    // 
    const div = document.getElementById("fileContent");
    div.ondragenter = div.ondragover = div.ondragleave = function() {
      // 
      return false;
    };
    // 
    div.addEventListener("drop",this.onDrop);
  },
  methods: {
    addSingle() {
      let id = getRandomStr();
      this.singleQuestion.push({
        id,
        title: "",
        options: { A: "", B: "", C: "", D: "" },
        answer: "",
        message: ""
      });
    },
    addMultiple() {
      let id = getRandomStr();
      this.multipleQuestion.push({
        id,
        title: "",
        options: { A: "", B: "", C: "", D: "" },
        answer: [],
        message: ""
      });
    },
    addJudgement() {
      let id = getRandomStr();
      this.judgementQuestion.push({
        id,
        title: "",
        answer: "",
        message: ""
      });
    },
    addAnswer() {
      let id = getRandomStr();
      this.answerQuestion.push({
        id,
        title: "",
        answer: "",
        message: ""
      });
    },
    async create() {
      if (
        this.singleQuestion.length == 0 ||
        this.multipleQuestion.length == 0 ||
        this.judgementQuestion.length == 0 ||
        this.answerQuestion.length == 0
      ) {
        this.$message({
          showClose: true,
          message: "",
          type: "warning"
        });
        return;
      }
      let flag = true;
      let singleAnswer = {};
      let multipleAnswer = {};
      let judgementAnswer = {};
      let answerAnswer = {};
      // 
      this.singleQuestion.forEach(item => {
        let message = null;
        if (!item.title) {
          message = "";
        } else if (
          !item.options["A"] ||
          !item.options["B"] ||
          !item.options["C"] ||
          !item.options["D"]
        ) {
          message = "";
        } else if (!item.answer) {
          message = "";
        }
        if (message) {
          flag = false;
        } else {
          singleAnswer[item.id] = item.answer;
        }
        item.message = message;
      });
      // 
      this.multipleQuestion.forEach(item => {
        let message = null;
        if (!item.title) {
          message = "";
        } else if (
          !item.options["A"] ||
          !item.options["B"] ||
          !item.options["C"] ||
          !item.options["D"]
        ) {
          message = "";
        } else if (item.answer.length <= 1) {
          message = "";
        }
        if (message) {
          flag = false;
        } else {
          multipleAnswer[item.id] = item.answer.sort().toString();
        }
        item.message = message;
      });
      // 
      this.judgementQuestion.forEach(item => {
        let message = null;
        if (!item.title) {
          message = "";
        } else if (!item.answer) {
          message = "";
        }
        if (message) {
          flag = false;
        } else {
          judgementAnswer[item.id] = item.answer;
        }
        item.message = message;
      });
      // 
      this.answerQuestion.forEach(item => {
        let message = null;
        if (!item.title) {
          message = "";
        } else if (!item.answer) {
          message = "";
        }
        if (message) {
          flag = false;
        } else {
          answerAnswer[item.id] = item.answer;
        }
        item.message = message;
      });

      if (!this.questionTitle.name) {
        this.questionTitle.message = "";
        flag = false;
      } else {
        this.questionTitle.message = "";
      }
      if (flag) {
        let params = {};
        params.title = this.questionTitle.name;
        params.single = {
          question: this.singleQuestion,
          answer: singleAnswer,
          count: this.singleQuestion.length
        };
        params.multiple = {
          question: this.multipleQuestion,
          answer: multipleAnswer,
          count: this.multipleQuestion.length
        };
        params.judgement = {
          question: this.judgementQuestion,
          answer: judgementAnswer,
          count: this.judgementQuestion.length
        };
        params.answer = {
          question: this.answerQuestion,
          answer: answerAnswer,
          count: this.answerQuestion.length
        };
        if (this.$route.params.flag) {
          params.flag = "1";
        }
        // 
        params.userName = this.userInfo.name;
        // id
        params.userId = this.userInfo._id;

        if (this.checkList.includes("")) {
          params.checkList = JSON.stringify(this.checkList);
        } else {
          params.checkList = JSON.stringify([...this.checkList, ""]);
        }

        params.questionType = this.questionType;

        try {
          const result = await post("/api/questions/addQuestion", params);
          if (result.statusCode == 200) {
            this.$message({
              type: "success",
              message: result.message,
              showClose: true
            });
            this.resetData();
          } else {
            this.$message({
              type: "warning",
              message: result.message,
              showClose: true
            });
          }
        } catch (error) {
          this.$message({
            type: "error",
            message: error.toString(),
            showClose: true
          });
        }
      } else {
        this.$message({
          type: "warning",
          message: "",
          showClose: true
        });
      }
    },
    onDrop(e){
      e.preventDefault();
      this.upload(e)
    },
    deleteItem({ key: item, index }) {
      this[item].splice(index, 1);
    },
    // 
    goBack() {
      this.$router.push({ name: "consultQuestions" });
    },
    back(cb) {
      if (
        !this.questionTitle.name &&
        this.singleQuestion.length == 0 &&
        this.multipleQuestion.length == 0 &&
        this.judgementQuestion.length == 0 &&
        this.answerQuestion.length == 0
      ) {
        if (cb && typeof cb == "function") {
          cb();
        }
      } else {
        this.openMessageBox(cb);
      }
    },
    openMessageBox(cb) {
      this.$confirm(", ", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          if (cb && typeof cb == "function") {
            cb();
          }
        })
        .catch(() => {});
    },
    openFile() {
      document.getElementById("file").click();
    },
    async upload(e) {
      try {
        let file = e.dataTransfer?e.dataTransfer.files[0]:e.target.files[0];
        let name = file.name;
        let reg = /(\.(xls)|(xlsx))$/i;
        let titleName = name.replace(/(.*\/)*([^.]+).*/gi, "$2");
        if (reg.test(name)) {
          let formData = new FormData();
          formData.append("file", file);
          const result = await post("/api/upload/uploadFile", formData);
          if (result.statusCode == 200) {
            let data = result.data.question;
            if (!this.questionTitle.name) {
              this.questionTitle.name = titleName;
            }
            for (let key in data) {
              data[key].forEach(item => {
                this[`${key}Question`].push(item);
              });
            }
            if (result.message) {
              this.$message({
                type: "error",
                message: result.message,
                showClose: true
              });
            } else {
              this.$message({
                type: "success",
                message: "",
                showClose: true
              });
            }
          } else {
            this.$message({
              type: "error",
              message: result.message,
              showClose: true
            });
          }
        } else {
          this.$message({
            type: "warning",
            message: ",xlsxlsx",
            showClose: true
          });
        }
      } catch (error) {
        this.$message({
          type: "error",
          message: error.toString(),
          showClose: true
        });
      } finally {
        this.$refs.pathClear.value = "";
      }
    },
    resetData() {
      this.questionTitle = {
        name: "",
        message: ""
      };
      this.singleQuestion = [];
      this.multipleQuestion = [];
      this.judgementQuestion = [];
      this.answerQuestion = [];
      this.questionType = "";
      this.checkList = [
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        ""
      ];
      storage.remove("AddQuestion");
    },
    async downLoadTemplate() {
      try {
        let url = "/api/downLoad/downLoadTemplate";
        window.open(url);
      } catch (error) {
        this.$message({
          showClose: true,
          type: "error",
          message: error.toString()
        });
      }
    }
  },
  computed: {
    ...mapGetters(["userInfo"])
  },
  beforeRouteLeave(to, from, next) {
    this.back(() => {
      const div = document.getElementById("fileContent")
      div.removeEventListener('drag',this.onDrop)
      next();
    });
  },
  watch: {
    singleQuestion: {
      deep: true,
      handler: function(newVal) {
        this.isSave++;
      }
    },
    multipleQuestion: {
      deep: true,
      handler: function(newVal) {
        this.isSave++;
      }
    },
    judgementQuestion: {
      deep: true,
      handler: function(newVal) {
        this.isSave++;
      }
    },
    answerQuestion: {
      deep: true,
      handler: function(newVal) {
        this.isSave++;
      }
    },
    questionTitle: {
      deep: true,
      handler: function(newVal) {
        this.isSave++;
      }
    },
    checkList: {
      deep: true,
      handler: function(newVal) {
        this.isSave++;
      }
    },
    questionType() {
      this.isSave++;
    },
    isSave() {
      storage.set("AddQuestion", {
        singleQuestion: this.singleQuestion,
        multipleQuestion: this.multipleQuestion,
        judgementQuestion: this.judgementQuestion,
        answerQuestion: this.answerQuestion,
        questionTitle: this.questionTitle,
        questionType: this.questionType,
        checkList: this.checkList
      });
    }
  }
};
</script>

<style lang="less" scoped>
.my-container {
  padding: 20px;
}
.min-width {
  min-width: 300px;
}
</style>


