<template>
    <div class="my-container">
        <el-dialog
            title=""
            :visible.sync="dialogVisible"
            width="30%"
            :before-close="handleClose">
            <div>
                <el-input
                v-model="commentInfo"
                type="textarea"
                placeholder=""></el-input>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button @click="cancel"> </el-button>
                <el-button type="primary" @click="ok"> </el-button>
            </span>
        </el-dialog>
        <div class="flex-row flex-wrap">
            <div class="flex-row flex-center min-width mt10">
            <span class="text-nowrap pr10 font18"> : </span>
            <el-input
                v-model="title"
                placeholder=""
                clearable>
            </el-input>
            </div>
            <div class="flex-row flex-center min-width mt10">
            <span class="text-nowrap pr10 pl10 font18"> : </span>
                <el-select clearable v-model="questionType" placeholder="">
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                </el-select>
            </div>
            <div class="flex-row flex-center min-width mt10">
                <span class="text-nowrap pr10 pl10 font18"> : </span>
                <el-date-picker
                    v-model="beginTime"
                    type="date"
                    placeholder="">
                </el-date-picker>
                <span class="pl10 pr10 font18">--</span>
                <el-date-picker
                    v-model="endTime"
                    type="date"
                    placeholder="">
                </el-date-picker>
            </div>
            <div class="flex-row flex-center ml10 mt10">
                <el-button type="primary" @click="search"></el-button>
            </div>
        </div>
        <div class="mt20">
            <el-table
                :data="questionsList"
                style="width: 100%">
                <el-table-column
                    type="index"
                    width="20">
                </el-table-column>
                <el-table-column
                    prop="title"
                    align='center'
                    label="">
                </el-table-column>
                <el-table-column
                    prop="single"
                    align='center'
                    label="(/)">
                    <template slot-scope="scope">
                        <div>{{`${scope.row.single.singleCorrect} / ${scope.row.single.singleCount}`}}</div>
                    </template>
                </el-table-column>
                <el-table-column
                    prop="multiple"
                    align='center'
                    label="(/)">
                    <template slot-scope="scope">
                        <div>{{`${scope.row.multiple.multipleCorrect} / ${scope.row.multiple.multipleCount}`}}</div>
                    </template>
                </el-table-column>
                <el-table-column
                    prop="judgement"
                    align='center'
                    label="(/)">
                    <template slot-scope="scope">
                        <div>{{`${scope.row.judgement.judgementCorrect} / ${scope.row.judgement.judgementCount}`}}</div>
                    </template>
                </el-table-column>
                <el-table-column
                    prop="answer"
                    align='center'
                    label="(/)">
                    <template slot-scope="scope">
                        <div>{{`${scope.row.answer.answerCorrect} / ${scope.row.answer.answerCount}`}}</div>
                    </template>
                </el-table-column>
                <el-table-column
                    prop="correctCount"
                    align='center'
                    label="">
                </el-table-column>
                <el-table-column
                    prop="totalCount"
                    align='center'
                    label="">
                </el-table-column>
                <el-table-column
                    prop="correctPercent"
                    align='center'
                    label="">
                    <template slot-scope="scope">
                        <div>{{scope.row.correctPercent | numToPercent}}</div>
                    </template>
                </el-table-column>
                <el-table-column
                    prop="userName"
                    align='center'
                    label="">
                </el-table-column>
                <el-table-column
                    prop="createTime"
                    align='center'
                    label="">
                    <template slot-scope="scope">
                        <div>{{scope.row.createTime | formatDate}}</div>
                    </template>
                </el-table-column>
                <el-table-column
                    prop="answerTime"
                    align='center'
                    label="">
                </el-table-column>
                <el-table-column label="" align='center' width="200">
                    <template slot-scope="scope">
                        <el-button
                        size="small"
                        type="success"
                        @click="comment(scope.$index, scope.row)"></el-button>
                        <el-button
                        type="primary"
                        size="small"
                        @click="handleClick(scope.$index, scope.row)"></el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>
        <div v-if="total>pageSize" class="text-center my-pagination">
            <el-pagination
                @current-change="handleCurrentChange"
                :current-page="currentPage"
                :page-size="pageSize"
                :layout='layout'
                :background='true'
                :total="total">
            </el-pagination>
        </div>
    </div>
</template>

<script>
import { get, post } from "../util/http.js";
import { mapGetters } from "vuex";
import Xss from 'xss'
export default {
  data() {
    return {
      questionsList: [],
      pageSize: 10,
      currentPage: 1,
      total: 0,
      layout: "total, prev, pager, next,jumper",
      title: "",
      beginTime: "",
      endTime: "",
      dialogVisible: false,
      questionId: "",
      commentInfo: "",
      questionType:'',
      questionTitle:''
    };
  },
  created() {
    this.getQuestionList();
  },
  methods: {
    async getQuestionList() {
      try {
        let params = {};
        if (this.beginTime && this.endTime && this.beginTime > this.endTime) {
          this.$message({
            showClose: true,
            message: "",
            type: "warning"
          });
          return;
        }
        if (this.beginTime) {
          params.beginTime = this.beginTime;
        }
        if (this.endTime) {
          params.endTime = this.endTime;
        }
        if (this.title) {
          params.title = this.title;
        }
        if (this.questionType) {
          params.questionType = this.questionType;
        }
        params.pageSize = this.pageSize;
        params.currentPage = this.currentPage;
        params.userId = this.userInfo._id;
        const result = await get("/api/questions/getAnswerQuestion", params);
        if (result.statusCode == 200) {
          this.questionsList = result.data.answerList;
          this.total = result.data.total;
        } else {
          this.$message({
            showClose: true,
            message: result.message,
            type: "warning"
          });
        }
      } catch (error) {
        this.$message({
          showClose: true,
          message: error.toString(),
          type: "error"
        });
      }
    },
    handleClick(index, row) {
      this.$router.push({ name: "answerDetail", params: {id:row.answerId} });
    },
    handleCurrentChange(e) {
      this.currentPage = e;
      this.getQuestionList();
    },
    comment(index, row) {
      this.questionId = row.questionId;
      this.dialogVisible = true;
      this.questionTitle = row.title
    },
    async ok() {
      try {
        if (!this.commentInfo) {
          this.$message({
            showClose: true,
            message: "",
            type: "warning"
          });
          return;
        } else {
          let params = {
            userName: this.userInfo.name,
            content: Xss(this.commentInfo),
            questionId: this.questionId,
            userId: this.userInfo._id,
            title:this.questionTitle
          };
          const result = await post("/api/comment/submitComment", params);
          if (result.statusCode == 200) {
            this.$message({
              showClose: true,
              message: result.message,
              type: "success"
            });
          } else {
            this.$message({
              showClose: true,
              message: result.message,
              type: "error"
            });
          }
          this.dialogVisible = false;
          this.commentInfo = "";
        }
      } catch (error) {
        this.$message({
          showClose: true,
          message: error.toString(),
          type: "error"
        });
        this.commentInfo = "";
        this.dialogVisible = false;
      }
    },
    cancel() {
      if (this.commentInfo) {
        this.$confirm("")
          .then(_ => {
            this.questionId = "";
            this.commentInfo = "";
            this.dialogVisible = false;
          })
          .catch(_ => {});
      } else {
        this.questionId = "";
        this.dialogVisible = false;
      }
    },
    handleClose(done) {
      if (this.commentInfo) {
        this.$confirm("")
          .then(_ => {
            this.questionId = "";
            this.commentInfo = "";
            done();
          })
          .catch(_ => {});
      } else {
        done();
      }
    },
    search(){
      this.currentPage = 1
      this.getQuestionList()
    }
  },
  computed: {
    ...mapGetters(["userInfo"])
  }
};
</script>

<style lang="less" scoped>
.my-container {
  padding: 20px;
  // position: relative;
  // height: calc(100% - 71px);
}
.min-width {
  min-width: 300px;
}
.text-nowrap {
  white-space: nowrap;
}
.my-pagination {
  margin: 30px 0;
}
</style>


