<template>
    <div class="my-container">
      <el-dialog
        title=""
        :visible.sync="dialogVisible"
        width="30%">
        <el-input
            v-model="content"
            placeholder=""
            type="textarea"
            clearable>
        </el-input>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false"> </el-button>
          <el-button type="primary" @click="ok"> </el-button>
        </span>
      </el-dialog>
      <el-dialog
        title=""
        :visible.sync="wxdialogVisible"
        width="20%">
        <canvas id="canvas"></canvas >
      </el-dialog>
        <div class="flex-row flex-wrap">
            <div class="flex-row flex-center min-width mt10">
            <span class="text-nowrap pr10 font18"> : </span>
            <el-input
                v-model="title"
                placeholder=""
                clearable>
            </el-input>
            </div>
            <div class="flex-row flex-center min-width mt10">
            <span class="text-nowrap pr10 pl10 font18"> : </span>
                <el-select clearable v-model="questionType" placeholder="">
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                </el-select>
            </div>
            <div class="flex-row flex-center min-width mt10">
                <span class="text-nowrap pr10 pl10 font18"> : </span>
                <el-date-picker
                    v-model="beginTime"
                    type="date"
                    placeholder="">
                </el-date-picker>
                <span class="pl10 pr10 font18">--</span>
                <el-date-picker
                    v-model="endTime"
                    type="date"
                    placeholder="">
                </el-date-picker>
            </div>
            <div class="flex-row flex-center ml10 mt10">
                <el-button type="primary" @click="search"></el-button>
            </div>
        </div>
        <div class="mt20">
            <el-table
                :data="questionsList"
                style="width: 100%">
                    <el-table-column
                    type="index"
                    width="50">
                </el-table-column>
                <el-table-column
                    align='center'
                    prop="title"
                    label="">
                </el-table-column>
                <el-table-column
                    align='center'
                    prop="singleCount"
                    label="">
                </el-table-column>
                <el-table-column
                    prop="multipleCount"
                    align='center'
                    label="">
                </el-table-column>
                <el-table-column
                    prop="judgementCount"
                    align='center'
                    label="">
                </el-table-column>
                <el-table-column
                    prop="answerCount"
                    align='center'
                    label="">
                </el-table-column>
                <el-table-column
                    prop="totalCount"
                    align='center'
                    label="">
                </el-table-column>
                <el-table-column
                    prop="questionType"
                    align='center'
                    label="">
                </el-table-column>
                <el-table-column
                    align='center'
                    prop="createTime"
                    label="">
                    <template slot-scope="scope">
                        <div>{{scope.row.createTime | formatDate}}</div>
                    </template>
                </el-table-column>
                <el-table-column label="" width="320" align='center'>
                    <template slot-scope="scope">
                        <div class="flex-row">
                            <el-button
                            size="small"
                            type="primary"
                            @click="handleEdit(scope.$index, scope.row)"></el-button>
                            <el-button
                            size="small"
                            type="success"
                            @click="handleDelete(scope.$index, scope.row)"></el-button>
                            <el-button
                            size="small"
                            type="warning"
                            @click="handleComment(scope.$index, scope.row)"></el-button>
                            <el-dropdown trigger="click" style="margin-left:10px;">
                            <el-button type="danger" size="small">
                              <i class="el-icon-arrow-down el-icon--right"></i>
                            </el-button>
                            <el-dropdown-menu slot="dropdown">
                              <el-dropdown-item><div @click='shareToQQ(scope.$index, scope.row)'>QQ</div></el-dropdown-item>
                              <el-dropdown-item><div @click='shareToWX(scope.$index, scope.row)'></div></el-dropdown-item>
                            </el-dropdown-menu>
                          </el-dropdown>
                        </div>
                    </template>
                </el-table-column>
            </el-table>
        </div>
        <div v-if="total>pageSize" class="text-center my-pagination">
            <el-pagination
                @current-change="handleCurrentChange"
                :current-page="currentPage"
                :page-size="pageSize"
                :layout='layout'
                :background='true'
                :total="total">
            </el-pagination>
        </div>
    </div>
</template>

<script>
import { get, post } from "../util/http.js";
import { shareUrl } from "../util/config.js";
import { mapGetters } from "vuex";
import QRCode from "qrcode";
export default {
  data() {
    return {
      questionsList: [],
      pageSize: 10,
      currentPage: 1,
      total: 0,
      layout: "total, prev, pager, next,jumper",
      title: "",
      userName: "",
      beginTime: "",
      endTime: "",
      questionType: "",
      dialogVisible: false,
      content: "",
      shareInfo: {},
      wxdialogVisible: false
    };
  },
  created() {
    this.getQuestionList();
  },
  methods: {
    async getQuestionList() {
      try {
        let params = {};
        if (this.beginTime && this.endTime && this.beginTime > this.endTime) {
          this.$message({
            showClose: true,
            message: "",
            type: "warning"
          });
          return;
        }
        if (this.beginTime) {
          params.beginTime = this.beginTime;
        }
        if (this.endTime) {
          params.endTime = this.endTime;
        }
        if (this.title) {
          params.title = this.title;
        }
        if (this.questionType) {
          params.questionType = this.questionType;
        }
        params.pageSize = this.pageSize;
        params.currentPage = this.currentPage;
        params.userId = this.userInfo._id;
        const result = await get("/api/questions/getPublishedQuestion", params);
        if (result.statusCode == 200) {
          this.questionsList = result.data.publishedQuestion;
          this.total = result.data.total;
        } else {
          this.$message({
            showClose: true,
            message: result.message,
            type: "warning"
          });
        }
      } catch (error) {
        this.$message({
          showClose: true,
          message: error.toString(),
          type: "error"
        });
      }
    },
    handleEdit(index, row) {
      this.$router.push({ name: "editQuestion", params: { id: row._id } });
    },
    handleDelete(index, row) {
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(async () => {
          try {
            const result = await post("/api/questions/deleteQuestion", {
              _id: row._id
            });
            if (result.statusCode == 200) {
              this.$message({
                showClose: true,
                type: "success",
                message: result.message
              });
              this.getQuestionList();
            } else {
              this.$message({
                showClose: true,
                type: "warning",
                message: result.message
              });
            }
          } catch (error) {
            this.$message({
              showClose: true,
              type: "error",
              message: error.toString()
            });
          }
        })
        .catch(() => {});
    },
    handleComment(index, row) {
      this.$router.push({
        name: "commentList",
        params: { id: row._id }
      });
    },
    handleCurrentChange(e) {
      this.currentPage = e;
      this.getQuestionList();
    },
    search() {
      this.currentPage = 1;
      this.getQuestionList();
    },
    shareToQQ(index, row) {
      this.dialogVisible = true;
      this.shareInfo = {
        title: row.title,
        id: row._id
      };
    },
    ok() {
      let obj_QQ = {
        url:
          shareUrl +
          `?id=${
            this.shareInfo.id
          }` /*URLQQ*/,
        desc: this
          .content /*(),|*/,
        title: "html5" /*()*/,
        summary: this.shareInfo.title /*()*/,
        // pics: shareDara.pic,   /*()*/
        // flash: '',    /*()*/
        site: this.userInfo.name /* () QQ*/,
        sharesource: "qq"
      };
      let s = [];
      for (let i in obj_QQ) {
        s.push(i + "=" + encodeURIComponent(obj_QQ[i] || ""));
      }
      let qqhref =
        "http://connect.qq.com/widget/shareqq/index.html?" + s.join("&");

      this.dialogVisible = false;
      this.shareInfo = {};
      setTimeout(() => {
        window.open(qqhref);
      }, 500);
    },
    shareToWX(index, row) {
      this.wxdialogVisible = true;
      this.$nextTick(() => {
        var canvas = document.getElementById("canvas");
        QRCode.toCanvas(canvas, shareUrl + `?id=${row._id}`);
      });
    }
  },
  computed: {
    ...mapGetters(["userInfo"])
  }
};
</script>

<style lang="less" scoped>
.my-container {
  padding: 20px;
}
.min-width {
  min-width: 300px;
}
.text-nowrap {
  white-space: nowrap;
}
.my-pagination {
  margin-top: 30px;
  margin-bottom: 20px;
}
</style>


