<template>
    <div style="height:87%;" class="my-container" ref="container">
        <el-dialog
          title=""
          :visible.sync="dialogVisible"
          width="50%">
          <div class="mb20 flex-row align-items">
            <h1 style="font-size:20px;"></h1>
            <el-select v-model="selectQuestion.questionType" placeholder="">
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
          </el-select>
          </div>
            <div v-if="selectQuestion.type == 0">
              <div class="flex-row align-items">
                <span class="font20 text-nowrap mr10"> : </span>
                <el-input v-model.trim="selectQuestion.question.title" placeholder=""></el-input>
            </div>
              <div class="mb10 mt10 flex-row" v-for="(i,option) in selectQuestion.question.options" :key="option">
                <el-radio  v-model.trim="selectQuestion.question.answer" :label="option" class="mr20">{{option}}</el-radio>
                <el-input v-model.trim="selectQuestion.question.options[option]" placeholder=""></el-input>
              </div>
            </div>
            <div v-if="selectQuestion.type == 1">
              <div class="flex-row align-items">
                <span class="font20 text-nowrap mr10"> : </span>
                <el-input v-model.trim="selectQuestion.question.title" placeholder=""></el-input>
            </div>
              <div class="mb10 mt10 flex-row align-items" v-for="(i,option) in selectQuestion.question.options" :key="option">
                <el-checkbox v-model.trim="selectQuestion.question.answer" :label="option" class="mr20">{{option}}</el-checkbox>
                <el-input v-model.trim="selectQuestion.question.options[option]" placeholder=""></el-input>
              </div>
            </div>
            <div v-if="selectQuestion.type == 2">
              <div class="flex-row align-items">
                <span class="font20 text-nowrap mr10"> : </span>
                <el-input v-model.trim="selectQuestion.question.title" placeholder=""></el-input>
            </div>
              <div class="mt20  mt10 flex-row">
                <el-radio v-model.trim="selectQuestion.question.answer" label="A" class="mr20"></el-radio>
                <el-radio v-model.trim="selectQuestion.question.answer" label="B" class="mr20"></el-radio>
              </div>
            </div>
            <div v-if="selectQuestion.type == 3">
              <div class="flex-row align-items">
                <span class="font20 text-nowrap mr10"> : </span>
                <el-input v-model.trim="selectQuestion.question.title" placeholder=""></el-input>
            </div>
               <div class="mt20 mt10 flex-row">
                <el-input
                    type="textarea"
                    placeholder=""
                    :autosize="{ minRows: 5, maxRows: 8}"
                    v-model.trim="selectQuestion.question.answer">
                </el-input>
              </div>
            </div>
            <div class="mt20">
                <h1 style="font-size:20px;margin-bottom:20px;"></h1>
                <el-checkbox-group v-model="selectQuestion.checkList">
                <el-checkbox label=""></el-checkbox>
                <el-checkbox label=""></el-checkbox>
                <el-checkbox label=""></el-checkbox>
                <el-checkbox label=""></el-checkbox>
                <el-checkbox label=""></el-checkbox>
                <el-checkbox label=""></el-checkbox>
                <el-checkbox label=""></el-checkbox>
              </el-checkbox-group>
            </div>
          <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible = false"> </el-button>
            <el-button type="primary" @click="ok"> </el-button>
          </span>
        </el-dialog>
        <div class="flex-row flex-wrap" ref="top">
            <div class="flex-row flex-center min-width mt10">
            <span class="text-nowrap pr10 font18"> : </span>
            <el-input
                v-model="title"
                placeholder=""
                clearable>
            </el-input>
            </div>
            <div class="flex-row flex-center min-width mt10">
            <span class="text-nowrap pr10 pl10 font18"> : </span>
                <el-select clearable v-model="questionType" placeholder="">
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                </el-select>
            </div>
            <div class="flex-row flex-center min-width mt10">
                <span class="text-nowrap pr10 pl10 font18"> : </span>
                <el-date-picker
                    v-model="beginTime"
                    type="date"
                    placeholder="">
                </el-date-picker>
                <span class="pl10 pr10 font18">--</span>
                <el-date-picker
                    v-model="endTime"
                    type="date"
                    placeholder="">
                </el-date-picker>
            </div>
            <div class="flex-row flex-center ml10 mt10">
                <el-button type="primary" @click="search"></el-button>
            </div>
        </div>
        <div class="mt20 flex-row">
           <div style="width:65%;">
              <el-table
                @row-click='rowClick'
                :data="questionsList"
                :height='height'
                style="width: 100%;">
                <el-table-column
                type="index"
                width="50">
                </el-table-column>
                <el-table-column
                    align='center'
                    prop="title"
                    :show-overflow-tooltip='true'
                    label="">
                </el-table-column>
                <el-table-column
                    align='center'
                    prop="type"
                    label="">
                    <template slot-scope="scope">
                        <div>{{scope.row.type==0?'':scope.row.type==1?'':scope.row.type==2?'':''}}</div>
                    </template>
                </el-table-column>
                <el-table-column
                    prop="questionType"
                    align='center'
                    label="">
                </el-table-column>
                <el-table-column
                    align='center'
                    prop="createTime"
                    label="">
                    <template slot-scope="scope">
                        <div>{{scope.row.createTime | formatDate}}</div>
                    </template>
                </el-table-column>
                <el-table-column
                    align='center'
                    label="">
                    <template slot-scope="scope">
                      <el-button v-if="userInfo._id == scope.row.userId" type='success' @click.stop="editBank(scope.$index, scope.row)"></el-button>
                      <el-button v-if="userInfo._id == scope.row.userId" type='warning' @click.stop="deleteBank(scope.$index, scope.row)"></el-button>
                      <p  v-if="userInfo._id != scope.row.userId"></p>
                    </template>
                </el-table-column>
            </el-table>
            <div ref="pagination" v-if="total>pageSize" class="text-center my-pagination">
            <el-pagination
                @current-change="handleCurrentChange"
                :current-page="currentPage"
                :page-size="pageSize"
                :layout='layout'
                :background='true'
                :total="total">
            </el-pagination>
        </div>
           </div>
           <div style="width:35%;" class="ml10">
             <div :style="{'overflow-y': 'scroll','height':`${height}px`}">
               <div class="mt20">
               <p></p>
               <div v-if="single.length==0" class="mt10"></div>
               <div @click="handelQuestion(item,'single')" :title="item.title" v-for="(item,index) in single" :key="index" class="flex-row mt10">
                 <span>{{index+1}}</span>
                 <span class="text-hiden">{{item.title}}</span>
               </div>
             </div>
             <div class="mt20">
               <p></p>
               <div v-if="multiple.length==0" class="mt10"></div>
               <div @click="handelQuestion(item,'multiple')" :title="item.title" v-for="(item,index) in multiple" :key="index" class="flex-row mt10">
                 <span>{{index+1}}</span>
                 <span class="text-hiden">{{item.title}}</span>
               </div>
             </div>
             <div class="mt20">
               <p></p>
               <div v-if="judgement.length==0" class="mt10"></div>
               <div  @click="handelQuestion(item,'judgement')" :title="item.title" v-for="(item,index) in judgement" :key="index" class="flex-row mt10">
                 <span>{{index+1}}</span>
                 <span class="text-hiden">{{item.title}}</span>
               </div>
             </div>
             <div class="mt20">
               <p></p>
               <div v-if="answer.length==0" class="mt10"></div>
               <div  @click="handelQuestion(item,'answer')" :title="item.title" v-for="(item,index) in answer" :key="index" class="flex-row mt10">
                 <span>{{index+1}}</span>
                 <span class="text-hiden">{{item.title}}</span>
               </div>
             </div>
             </div>
             <div class="text-center mt20">
               <el-button @click="addQuestion" type='primary'></el-button>
             </div>
           </div>
        </div>
    </div>
</template>

<script>
import { get, post } from "../util/http.js";
import { mapGetters } from "vuex";
export default {
  data() {
    return {
      questionsList: [],
      pageSize: 10,
      currentPage: 1,
      total: 0,
      layout: "total, prev, pager, next,jumper",
      title: "",
      userName: "",
      beginTime: "",
      endTime: "",
      questionType: "",
      height:0,
      // 
      single: [],
      // 
      multiple: [],
      // 
      judgement: [],
      // 
      answer: [],
      dialogVisible:false,
      selectQuestion:{}
    };
  },
  created() {
    this.getQuestionList();
  },
  mounted(){
    this.$nextTick(()=>{
      this.onResize()
      window.addEventListener('resize',this.onResize)
    })
  },
  methods: {
    onResize(){
      let h1 = this.$refs.container.offsetHeight
      let h2 = this.$refs.top.offsetHeight
      let h3 = 0
      if(this.$refs.pagination){
        h3 = this.$refs.pagination.offsetHeight
      }
      this.height = h1-h2-h3-90
    },
    async getQuestionList() {
      try {
        let params = {};
        if (this.beginTime && this.endTime && this.beginTime > this.endTime) {
          this.$message({
            showClose: true,
            message: "",
            type: "warning"
          });
          return;
        }
        if (this.beginTime) {
          params.beginTime = this.beginTime;
        }
        if (this.endTime) {
          params.endTime = this.endTime;
        }
        if (this.title) {
          params.title = this.title;
        }
        if (this.questionType) {
          params.questionType = this.questionType;
        }
        params.pageSize = this.pageSize;
        params.currentPage = this.currentPage;
        params.userId = this.userInfo._id;
        params.checkList = this.userInfo.identity;
        const result = await get("/api/questions/getItemBank", params);
        if (result.statusCode == 200) {
          this.questionsList = result.data.itemList;
          this.total = result.data.total;
        } else {
          this.$message({
            showClose: true,
            message: result.message,
            type: "warning"
          });
        }
      } catch (error) {
        this.$message({
          showClose: true,
          message: error.toString(),
          type: "error"
        });
      }
    },
    handleCurrentChange(e) {
      this.currentPage = e;
      this.getQuestionList();
    },
    search() {
      this.currentPage = 1;
      this.getQuestionList();
    },
    rowClick(e){
      if(e.type==0){
        this.handelQuestion(e,'single')
      }else if(e.type == 1){
        this.handelQuestion(e,'multiple')
      }else if(e.type == 2){
        this.handelQuestion(e,'judgement')
      }else if(e.type == 3){
        this.handelQuestion(e,'answer')
      }
    },
    handelQuestion(e,type){
      if(e.question){
        let index = this[type].findIndex(item=>item.id==e.question.id)
        if(index == -1){
          this[type].push(e.question)
        }else{
          this[type].splice(index, 1);
        }
      }else{
        let index = this[type].findIndex(item=>item.id==e.id)
        if(index == -1){
          this[type].push(e)
        }else{
          this[type].splice(index, 1);
        }
      }
    },
    addQuestion(){
      this.$router.push({
        name:'addQuestion',
        params:{
          flag:'1',
          single:this.single,
          multiple:this.multiple,
          judgement:this.judgement,
          answer:this.answer
        }
      })
    },
    deleteBank(index,row){
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(async () => {
          try {
            const result = await get("/api/questions/deleteBank", {
              id: row._id
            });
            if (result.statusCode == 200) {
              this.$message({
                showClose: true,
                type: "success",
                message: result.message
              });
              this.getQuestionList();
            } else {
              this.$message({
                showClose: true,
                type: "warning",
                message: result.message
              });
            }
          } catch (error) {
            this.$message({
              showClose: true,
              type: "error",
              message: error.toString()
            });
          }
        })
        .catch(() => {});
    },
    editBank(index,row){
      this.selectQuestion = Object.assign({},row,{checkList:JSON.parse(row.checkList)})
      this.dialogVisible = true
    },
    async ok(){
       try {
            const result = await post("/api/questions/editBank", {
              question:Object.assign({},this.selectQuestion,{
                checkList:JSON.stringify(this.selectQuestion.checkList),
                title:this.selectQuestion.question.title
              })
            });
            if (result.statusCode == 200) {
              this.dialogVisible = false
              this.$message({
                showClose: true,
                type: "success",
                message: result.message
              });
              this.getQuestionList();
            } else {
              this.$message({
                showClose: true,
                type: "warning",
                message: result.message
              });
            }
          } catch (error) {
            this.$message({
              showClose: true,
              type: "error",
              message: error.toString()
            });
          }
    }
  },
  beforeDestroy(){
    window.removeEventListener('resize',this.onResize)
  },
  computed: {
    ...mapGetters(["userInfo"])
  }
};
</script>

<style lang="less" scoped>
.my-container {
  padding: 20px;
}
.min-width {
  min-width: 300px;
}
.text-nowrap {
  white-space: nowrap;
}
.my-pagination {
  margin-top: 30px;
  margin-bottom: 20px;
}
</style>


