<template>
    <div class="mt20" style="border-bottom:1px solid #ccc;">
        <h1 class="font20"></h1>
        <div v-if="isAnswer&&multipleQuestion.length==0" class="mt20"></div>
        <div class="mt30" v-for="(question,index) in multipleQuestion" :key="index">
        <div class="flex-row align-items">
            <span class="font18">{{index+1}}</span>
            <span v-if='!isAnswer' class="font20 text-nowrap mr10"> : </span>
            <el-input v-if='!isAnswer' v-model.trim="question.title" placeholder=""></el-input>
            <span v-if='isAnswer' class="font20">{{question.title}}</span>
        </div>
        <div class="mt20 ml20">
            <div class="mb10 flex-row align-items" v-for="(i,option) in question.options" :key="option">
                <el-checkbox :disabled="disabled" v-model.trim="question.answer" :label="option" class="mr20">{{option}}</el-checkbox>
                <el-input v-if='!isAnswer' v-model.trim="question.options[option]" placeholder=""></el-input>
                <span v-if='isAnswer' class="font18">{{question.options[option]}}</span>
            </div>
        </div>
        <div class="mt20 flex-row item">
            <span style="color:red;">{{question.message}}</span>
            <div v-if='!isAnswer' @click="deleteItem('multipleQuestion',index)" class="deleteItem"></div>
        </div>
    </div>
    <el-button v-if='!isAnswer' style="margin-top:20px;" type="danger" @click="addMultiple"></el-button>
    </div>
</template>

<script>
export default {
  props: {
    multipleQuestion: {
      type: Array,
      default: function() {
        return [];
      }
    },
    isAnswer: {
      type: Boolean,
      default: false
    },
    disabled: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    deleteItem(key, index) {
      this.$emit("deleteItem", { key, index });
    },
    addMultiple() {
      this.$emit("addMultiple");
    }
  }
};
</script>
