import {
  formatDate,
  numToPercent
} from './util/index'

// 
Vue.filter('formatDate', formatDate)
Vue.filter('numToPercent', numToPercent)

// 
import Bg from './components/Bg.vue'
import Single from './components/Single'
import Multiple from './components/Multiple'
import Judgement from './components/Judgement'
import Answer from './components/Answer'

Vue.component('bg', Bg)
Vue.component('Single', Single)
Vue.component('Multiple', Multiple)
Vue.component('Answer', Answer)
Vue.component('Judgement', Judgement)

import axios from 'axios'

import {
  whiteList
} from './util/config';

// 
axios.interceptors.request.use(function (config) {
  let url = config.url
  if (!whiteList.some(item => url.startsWith(item))) {
    let token = window.sessionStorage.getItem('token') || ''
    config.headers.token = token
  }
  return config;
})
axios.interceptors.response.use(function (config) {
  if (config.data.statusCode == 401) {
    window.sessionStorage.removeItem('token')
    store.commit('setUserInfo', {})
    router.replace({
      name: 'login'
    })
  } else {
    let token = config.headers.token
    if (token) {
      window.sessionStorage.setItem('token', token)
    }
  }

  return config;
})


Vue.config.productionTip = false


// 
router.beforeEach((to, from, next) => {
  let userInfo = store.state.userInfo
  // 
  if (!userInfo._id) {
    // 
    if (to.name == 'login' || to.name == 'register') {
      next()
    } else {
      next({
        name: 'login'
      })
    }
  } else { //
    let flag = to.meta.flag
    if (flag) {
      let type = to.meta.type
      const index = userInfo.jurisdiction.includes(type)
      if (index) {
        next()
      } else {
        next({
          name: 'notFound'
        })
      }
    } else {
      next()
    }
  }
})

//
Notification.requestPermission(function (status) {
  //  Chrome/Safari  Notification.permission
  if (Notification.permission !== status) {
    Notification.permission = status;
  }
});


window.addEventListener('offline', function () {
  //
  var n = new Notification('', {
    body: '',
    tag: 'avenstar', //tag
    icon: img,
    requireInteraction: false //false
  })
})

new Vue({
  router,
  store,
  mounted() {
    document.dispatchEvent(new Event('render-event'))
  },
  render: h => h(App)
}).$mount('#app')