<template>
    <div class="my-container">
      <div class="mt20 flex-row align-items">
          <h1 style="font-size:20px;"></h1>
          <el-select v-model="questionType" placeholder="">
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
        </el-select>
        </div>
        <div class="mt20">
            <div class="flex-row flex-center">
                <span class="text-nowrap mr10 font20"> :</span>
                <el-input v-model.trim="questionTitle.name" clearable placeholder=""></el-input>
            </div>
            <div style="margin:10px 0 0 100px;">
                <span style="color:red;">{{questionTitle.message}}</span>
            </div>
        </div>
        <!--  -->
        <Single 
        :singleQuestion='singleQuestion'
        @addSingle='addSingle' 
        @deleteItem='deleteItem'></Single>
        <!--  -->
        <Multiple 
        :multipleQuestion='multipleQuestion'
        @addMultiple='addMultiple' 
        @deleteItem='deleteItem'></Multiple>
        <!--  -->
        <Judgement 
        :judgementQuestion='judgementQuestion'
        @addJudgement='addJudgement' 
        @deleteItem='deleteItem'></Judgement>
        <!--  -->
        <Answer 
        :answerQuestion='answerQuestion'
        @addAnswer='addAnswer' 
        @deleteItem='deleteItem'></Answer>
        <div class="mt20">
          <h1 style="font-size:20px;margin-bottom:20px;"></h1>
          <el-checkbox-group v-model="checkList">
          <el-checkbox label=""></el-checkbox>
          <el-checkbox label=""></el-checkbox>
          <el-checkbox label=""></el-checkbox>
          <el-checkbox label=""></el-checkbox>
          <el-checkbox label=""></el-checkbox>
          <el-checkbox label=""></el-checkbox>
          <el-checkbox label=""></el-checkbox>
        </el-checkbox-group>
        </div>
        <div class="mt20">
            <el-button type="primary" @click="save"></el-button>
            <el-button type="success" @click="goBack"></el-button>
        </div>
    </div>
</template>

<script>
import { mapGetters } from "vuex";
import { post, get } from "../util/http.js";
export default {
  data() {
    return {
      // 
      questionTitle: {
        name: "",
        message: ""
      },
      // 
      singleQuestion: [],
      // 
      multipleQuestion: [],
      // 
      judgementQuestion: [],
      // 
      answerQuestion: [],
      _id: "",
      // 
      isEdit: false,
      // 
      isSave: false,
      questionType:[],
      checkList:[]
    };
  },
  created() {
    this.getQuestionDetail();
  },
  methods: {
    async getQuestionDetail() {
      try {
        let { id } = this.$route.params;
        const result = await get(
          "/api/questions/getPublishedQuestionById/" + id
        );
        if (result.statusCode == 200) {
          this.questionTitle.name = result.data.questionDetail.title;
          this.singleQuestion = result.data.questionDetail.single;
          this.multipleQuestion = result.data.questionDetail.multiple;
          this.judgementQuestion = result.data.questionDetail.judgement;
          this.answerQuestion = result.data.questionDetail.answer;
          this.checkList = JSON.parse(result.data.questionDetail.checkList)
          this.questionType = result.data.questionDetail.questionType
        } else {
          this.$message({
            showClose: true,
            message: result.message,
            type: "warning"
          });
        }
      } catch (error) {
        this.$message({
          showClose: true,
          message: error.toString(),
          type: "error"
        });
      } finally {
        this.$nextTick(() => {
          this.isEdit = false;
        });
      }
    },
    addSingle() {
      let id = Date.now().toString();
      this.singleQuestion.push({
        id,
        title: "",
        options: { A: "", B: "", C: "", D: "" },
        answer: "",
        message: ""
      });
    },
    addMultiple() {
      let id = Date.now().toString();
      this.multipleQuestion.push({
        id,
        title: "",
        options: { A: "", B: "", C: "", D: "" },
        answer: [],
        message: ""
      });
    },
    addJudgement() {
      let id = Date.now().toString();
      this.judgementQuestion.push({
        id,
        title: "",
        answer: "",
        message: ""
      });
    },
    addAnswer() {
      let id = Date.now().toString();
      this.answerQuestion.push({
        id,
        title: "",
        answer: "",
        message: ""
      });
    },
    async save() {
      if (
        this.singleQuestion.length == 0 &&
        this.multipleQuestion.length == 0 &&
        this.judgementQuestion.length == 0 &&
        this.answerQuestion == 0
      ) {
        this.$message({
          showClose: true,
          message: "",
          type: "warning"
        });
        return;
      }
      let flag = true;
      let singleAnswer = {};
      let multipleAnswer = {};
      let judgementAnswer = {};
      let answerAnswer = {};
      // 
      this.singleQuestion.forEach(item => {
        let message = null;
        if (!item.title) {
          message = "";
        } else if (
          !item.options["A"] ||
          !item.options["B"] ||
          !item.options["C"] ||
          !item.options["D"]
        ) {
          message = "";
        } else if (!item.answer) {
          message = "";
        }
        if (message) {
          flag = false;
        } else {
          singleAnswer[item.id] = item.answer;
        }
        item.message = message;
      });
      // 
      this.multipleQuestion.forEach(item => {
        let message = null;
        if (!item.title) {
          message = "";
        } else if (
          !item.options["A"] ||
          !item.options["B"] ||
          !item.options["C"] ||
          !item.options["D"]
        ) {
          message = "";
        } else if (item.answer.length <= 1) {
          message = "";
        }
        if (message) {
          flag = false;
        } else {
          multipleAnswer[item.id] = item.answer.sort().toString();
        }
        item.message = message;
      });
      // 
      this.judgementQuestion.forEach(item => {
        let message = null;
        if (!item.title) {
          message = "";
        } else if (!item.answer) {
          message = "";
        }
        if (message) {
          flag = false;
        } else {
          judgementAnswer[item.id] = item.answer;
        }
        item.message = message;
      });
      // 
      this.answerQuestion.forEach(item => {
        let message = null;
        if (!item.title) {
          message = "";
        } else if (!item.answer) {
          message = "";
        }
        if (message) {
          flag = false;
        } else {
          answerAnswer[item.id] = item.answer;
        }
        item.message = message;
      });

      if (!this.questionTitle.name) {
        this.questionTitle.message = "";
        flag = false;
      } else {
        this.questionTitle.message = "";
      }
      if (flag) {
        let params = {};
        params.title = this.questionTitle.name;
        params.single = {
          question: this.singleQuestion,
          answer:singleAnswer,
          count: this.singleQuestion.length
        };
        params.multiple = {
          question: this.multipleQuestion,
          answer: multipleAnswer,
          count: this.multipleQuestion.length
        };
        params.judgement = {
          question: this.judgementQuestion,
          answer: judgementAnswer,
          count: this.judgementQuestion.length
        };
        params.answer = {
          question: this.answerQuestion,
          answer: answerAnswer,
          count: this.answerQuestion.length
        };
        // 
        params.userName = this.userInfo.name;
        // id
        params.userId = this.userInfo._id;
        // id
        params._id = this.$route.params.id;

        if(this.checkList.includes('')){
          params.checkList = JSON.stringify(this.checkList)
        }else{
          params.checkList = JSON.stringify([...this.checkList,''])
        }

        params.questionType = this.questionType

        try {
          const result = await post("/api/questions/editQuestion", params);
          if (result.statusCode == 200) {
            this.$message({
              type: "success",
              message: result.message,
              showClose: true
            });
            this.isSave = true;
            this.$router.back();
          } else {
            this.$message({
              type: "warning",
              message: result.message,
              showClose: true
            });
          }
        } catch (error) {
          this.$message({
            type: "error",
            message: error.toString(),
            showClose: true
          });
        }
      } else {
        this.$message({
          type: "warning",
          message: "",
          showClose: true
        });
      }
    },
    deleteItem({ key: item, index }) {
      this[item].splice(index, 1);
    },
    // 
    goBack() {
      this.$router.push({ name: "publishedQuestions" });
    },
    back(cb) {
      if (
        !this.questionTitle.name &&
        this.singleQuestion.length == 0 &&
        this.multipleQuestion.length == 0 &&
        this.judgementQuestion.length == 0 &&
        this.answerQuestion == 0
      ) {
        if (cb && typeof cb == "function") {
          cb();
        }
      } else {
        if (this.isEdit) {
          this.openMessageBox(cb);
        } else {
          if (cb && typeof cb == "function") {
            cb();
          }
        }
      }
    },
    openMessageBox(cb) {
      this.$confirm(", ", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          if (cb && typeof cb == "function") {
            cb();
          }
        })
        .catch(() => {});
    }
  },
  beforeRouteLeave(to, from, next) {
    if (this.isSave) {
      next();
    } else {
      this.back(() => {
        next();
      });
    }
  },
  watch: {
    questionTitle: {
      deep: true,
      handler: function() {
        this.isEdit = true;
      }
    },
    singleQuestion: {
      deep: true,
      handler: function() {
        this.isEdit = true;
      }
    },
    multipleQuestion: {
      deep: true,
      handler: function() {
        this.isEdit = true;
      }
    },
    judgementQuestion: {
      deep: true,
      handler: function() {
        this.isEdit = true;
      }
    },
    answerQuestion: {
      deep: true,
      handler: function() {
        this.isEdit = true;
      }
    }
  },
  computed: {
    ...mapGetters(["userInfo"])
  }
};
</script>

<style lang="less" scoped>
.my-container {
  padding: 20px;
}
.min-width {
  min-width: 300px;
}
</style>


