<template>
    <section class="form_container" @keydown.enter="submitForm('registerForm')">
        <div class="manage_tip">
            <span class="title"></span>
        </div>
        <el-form :model="registerUser" :rules="rules" class="registerForm" ref="registerForm" label-width="80px">
            <el-form-item label="" prop="name">
                <el-input v-model="registerUser.name" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="email">
                <el-input v-model="registerUser.email" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="password">
                <el-input v-model="registerUser.password" placeholder="" type="password"></el-input>
            </el-form-item>
            <el-form-item label="" prop="password2">
                <el-input v-model="registerUser.password2" placeholder="" type="password"></el-input>
            </el-form-item>
            <el-form-item label="" prop="code">
                <div class="flex-row">
                  <el-input v-model="registerUser.code" placeholder=""></el-input>
                <el-button :disabled="disabled" @click="getCode" type="primary" class="code_btn">{{!disabled?'':`${second}s`}}</el-button>
                </div>
            </el-form-item>
             <el-form-item label="" prop="identity">
              <el-select v-model="registerUser.identity" placeholder="">
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
                <el-option label="" value=""></el-option>
              </el-select>
            </el-form-item>
            <el-form-item>
                <el-button type="primary"  class="submit_btn" @click="submitForm('registerForm')"> </el-button>
            </el-form-item>
            <div class="tiparea">
                <p><router-link to='/login'></router-link></p>
            </div>
        </el-form>
    </section>
</template>

<script>
import { mapMutations } from "vuex";
import { checkEmail } from "../util/index.js";
import { post, get } from "../util/http.js";
export default {
  name: "register",
  data() {
    var validatePass2 = (rule, value, callback) => {
      if (value !== this.registerUser.password) {
        callback(new Error("!"));
      } else {
        callback();
      }
    };
    return {
      registerUser: {
        name: "",
        email: "",
        password: "",
        password2: "",
        code: "",
        identity:''
      },
      disabled: false,
      second: 60,
      // 
      rules: {
        name: [
          {
            required: true,
            message: "",
            // 
            trigger: "blur"
          },
          {
            min: 2,
            max: 30,
            message: "230",
            trigger: "blur"
          }
        ],
        email: [
          {
            type: "email",
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        password: [
          {
            required: true,
            trigger: "blur",
            message: ""
          },
          {
            min: 6,
            max: 30,
            message: "630",
            trigger: "blur"
          }
        ],
        password2: [
          {
            required: true,
            trigger: "blur",
            message: ""
          },
          {
            min: 6,
            max: 30,
            message: "630",
            trigger: "blur"
          },
          {
            validator: validatePass2,
            trigger: "blur"
          }
        ],
        code: [
          {
            required: true,
            message: "",
            // 
            trigger: "blur"
          },
          {
            min: 4,
            max: 4,
            message: "4",
            trigger: "blur"
          }
        ],
        identity: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ]
      }
    };
  },
  methods: {
    ...mapMutations(["setUserInfo"]),
    // 
    submitForm(formName) {
      this.$refs[formName].validate(async valid => {
        if (valid) {
          try {
            const result = await post("/api/user/register", this.registerUser);
            if (result.statusCode == 200) {
              this.$message({
                showClose: true,
                message: result.message,
                type: "success"
              });
              this.setUserInfo(result.data.userInfo);
              this.$router.push({
                name: "home"
              });
            } else {
              this.$message({
                showClose: true,
                message: result.message,
                type: "warning"
              });
            }
          } catch (error) {
            this.$message({
              showClose: true,
              message: error.toString(),
              type: "error"
            });
          }
        }
      });
    },
    // 
    getCode() {
      if (this.registerUser.email == "") {
        this.$message({
          showClose: true,
          message: "",
          type: "warning"
        });
        return;
      }
      if (!checkEmail(this.registerUser.email)) {
        return;
      }
      this.disabled = true;
      this.timer = setInterval(() => {
        this.second = this.second - 1;
        if (this.second === 0) {
          this.disabled = false;
          this.second = 60;
          clearInterval(this.timer);
        }
      }, 1000);

      get("/api/user/sendCode", { email: this.registerUser.email })
        .then(res => {
          if (res.statusCode == 200) {
            this.$message({
              showClose: true,
              message: res.message,
              type: "success"
            });
          } else {
            this.$message({
              showClose: true,
              message: res.message,
              type: "warning"
            });
          }
        })
        .catch(error => {
          this.$message({
            showClose: true,
            message: error.toString(),
            type: "error"
          });
        });
    }
  },
  beforeDestroy() {
    if (this.timer) {
      clearInterval(this.timer);
    }
  }
};
</script>


<style scoped>
.form_container {
  width: 370px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  padding: 25px;
  border-radius: 5px;
  text-align: center;
}
.form_container .manage_tip .title {
  font-family: "Microsoft YaHei";
  font-weight: bold;
  font-size: 26px;
  color: #fff;
}
.registerForm {
  margin-top: 20px;
  background-color: rgba(0, 0, 0, .3);
  padding: 20px 40px 20px 20px;
  border-radius: 5px;
  box-shadow: 0px 5px 10px #cccc;
}

.submit_btn {
  width: 100%;
}

.tiparea {
  text-align: right;
  font-size: 12px;
  color: #fff;
}
.tiparea p a {
  color: #409eff;
}
.code_btn {
  width: 130px;
  padding: 0;
  margin-left: 5px;
}
</style>



