<template>
  <div class="my-container">
    <div class="time">
      
      <span style="color:red;">{{showTime}}</span>
    </div>
    <div class="flex-row flex-wrap">
      <div class="flex-row flex-center min-width mt10">
        <span class="text-nowrap pr10 font18"> :</span>
        <el-input v-model="singleCount" placeholder="" clearable></el-input>
      </div>
      <div class="flex-row flex-center min-width mt10">
        <span class="text-nowrap pr10 pl10 font18"> :</span>
        <el-input v-model="multipleCount" placeholder="" clearable></el-input>
      </div>
      <div class="flex-row flex-center min-width mt10">
        <span class="text-nowrap pr10 pl10 font18"> :</span>
        <el-input v-model="judgementCount" placeholder="" clearable></el-input>
      </div>
    </div>
    <div class="flex-row flex-wrap">
      <div class="flex-row flex-center min-width mt10">
        <span class="text-nowrap pr10 font18"> :</span>
        <el-input v-model="answerCount" placeholder="" clearable></el-input>
      </div>
      <div class="flex-row flex-center min-width mt10">
        <span class="text-nowrap pr10 pl10 font18"> :</span>
        <el-select clearable v-model="questionType" placeholder="">
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
        </el-select>
      </div>
      <div class="flex-row flex-center ml10 mt10">
        <el-button type="primary" @click="create"></el-button>
      </div>
    </div>
    <!--  -->
    <Single v-if="singleQuestion.length" :isAnswer="isAnswer" :singleQuestion="singleQuestion"></Single>
    <!--  -->
    <Multiple
      v-if="multipleQuestion.length"
      :isAnswer="isAnswer"
      :multipleQuestion="multipleQuestion"
    ></Multiple>
    <!--  -->
    <Judgement
      v-if="judgementQuestion.length"
      :isAnswer="isAnswer"
      :judgementQuestion="judgementQuestion"
    ></Judgement>
    <!--  -->
    <Answer v-if="answerQuestion.length" :isAnswer="isAnswer" :answerQuestion="answerQuestion"></Answer>
    <div class="mt20" v-if="showBtn">
      <el-button type="primary" @click="submit"></el-button>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import { post, get } from "../util/http.js";
import { getDate } from "../util";
export default {
  data() {
    return {
      // 
      singleQuestion: [],
      singleCount: 2,
      // 
      multipleQuestion: [],
      multipleCount: 2,
      // 
      judgementQuestion: [],
      judgementCount: 2,
      // 
      answerQuestion: [],
      answerCount: 2,
      isAnswer: true,
      time: 0,
      questionType: ""
    };
  },
  methods: {
    async submit() {
      let flag = true;
      let answer = {};
      let singleAnswer = {};
      let multipleAnswer = {};
      let judgementAnswer = {};
      let answerAnswer = {};
      // // 
      // this.singleQuestion.forEach(item => {
      //     singleAnswer[item.id] = item.answer;
      // });
      // // 
      // this.multipleQuestion.forEach(item => {   
      //     multipleAnswer[item.id] = item.answer.sort().toString();
      // });
      // // 
      // this.judgementQuestion.forEach(item => {
      //     judgementAnswer[item.id] = item.answer;
      // });
      // // 
      // this.answerQuestion.forEach(item => {
      //     answerAnswer[item.id] = item.answer;
      // });

      let s = []
      // 
      this.singleQuestion.forEach(item => {
        singleAnswer[item.id] = item.a;
        let message = null;
        if (!item.answer) {
          message = "";
        }
        if (message) {
          flag = false;
        } else {
          answer[item.id] = item.answer;
        }
        item.message = message;
        s.push({
          ...item,
          answer:item.a
        })
      });

      let m = []
      // 
      this.multipleQuestion.forEach(item => {
        multipleAnswer[item.id] = item.a.sort().toString();
        let message = null;
        if (item.answer.length == 0) {
          message = "";
        }
        if (message) {
          flag = false;
        } else {
          answer[item.id] = item.answer;
        }
        item.message = message;
        m.push({
          ...item,
          answer:item.a
        })
      });

      let j = []
      // 
      this.judgementQuestion.forEach(item => {
        judgementAnswer[item.id] = item.a;
        let message = null;
        if (!item.answer) {
          message = "";
        }
        if (message) {
          flag = false;
        } else {
          answer[item.id] = item.answer;
        }
        item.message = message;
        j.push({
          ...item,
          answer:item.a
        })
      });

      let a = []
      // 
      this.answerQuestion.forEach(item => {
        answerAnswer[item.id] = item.a;
        let message = null;
        if (!item.answer) {
          message = "";
        }
        if (message) {
          flag = false;
        } else {
          answer[item.id] = item.answer;
        }
        item.message = message;
        a.push({
          ...item,
          answer:item.a
        })
      });
      if (!flag) {
        this.$message({
          type: "warning",
          message: "",
          showClose: true
        });
      }
      if (flag) {
        let params = {};
        params.title = `(${getDate()})`;
        params.answer = answer;
        // 
        params.userName = this.userInfo.name;
        // id
        params.userId = this.userInfo._id;
        params.answerTime = this.showTime;
        params.questionType = this.questionType;
        params.singleQuestion = {
          question: s,
          answer: singleAnswer,
          count: s.length
        }
        params.multipleQuestion = {
          question: m,
          answer: multipleAnswer,
          count: m.length
        };
        params.judgementQuestion = {
          question: j,
          answer: judgementAnswer,
          count: j.length
        };
        params.answerQuestion = {
          question: a,
          answer: answerAnswer,
          count: a.length
        };
        params.flag = '2'
        try {
          const result = await post("/api/questions/submitQuestion", params);
          if (result.statusCode == 200) {
            this.$message({
              type: "success",
              message: result.message,
              showClose: true
            });
            this.$router.push({
                name:'answerDetail',
                params:{id:result.data.result._id}
            })
          } else {
            this.$message({
              type: "warning",
              message: result.message,
              showClose: true
            });
          }
        } catch (error) {
          this.$message({
            type: "error",
            message: error.toString(),
            showClose: true
          });
        }
      }
    },
    initTime() {
      this.timer = setInterval(() => {
        this.time++;
      }, 1000);
    },
    async create() {
      try {
        let params = {
          userId: this.userInfo._id,
          singleCount: this.singleCount,
          multipleCount: this.multipleCount,
          judgementCount: this.judgementCount,
          answerCount: this.answerCount,
          questionType: this.questionType,
          checkList:this.userInfo.identity
        };
        const result = await get("/api/questions/createExercises", params);
        if (result.statusCode == 200) {
          this.singleQuestion = result.data.exercises.single;
          this.multipleQuestion = result.data.exercises.multiple;
          this.judgementQuestion = result.data.exercises.judgement;
          this.answerQuestion = result.data.exercises.answer;
          if (!this.showBtn) {
            this.$message({
              type: "warning",
              message: "",
              showClose: true
            });
            return;
          }
          if (this.timer) {
            clearInterval(this.timer);
          }
          this.initTime();
        } else {
          this.$message({
            type: "warning",
            message: result.message,
            showClose: true
          });
        }
      } catch (error) {
        this.$message({
          type: "error",
          message: error.toString(),
          showClose: true
        });
      }
    }
  },
  beforeDestroy() {
    if (this.timer) {
      clearInterval(this.timer);
    }
  },
  computed: {
    ...mapGetters(["userInfo"]),
    showTime() {
      let s = this.time % 60;
      s = s < 10 ? `0${s}` : s;
      let m = parseInt(this.time / 60) % 60;
      m = m < 10 ? `0${m}` : m;
      let h = parseInt(this.time / 60 / 60) % 24;
      h = h < 10 ? `0${h}` : h;
      return `${h}${m}${s}`;
    },
    showBtn() {
      return (
        this.singleQuestion.length ||
        this.multipleQuestion.length ||
        this.judgementQuestion.length ||
        this.answerQuestion.length
      );
    }
  }
};
</script>

<style lang="less" scoped>
.my-container {
  padding: 20px;
}
.min-width {
  min-width: 300px;
}
.time {
  position: fixed;
  right: 50px;
  top: 100px;
  font-size: 20px;
}
</style>


